/*      */
export const handleTrailerVisible = (trailer, videoId) => {
   if (trailer === false) {
      $(`.${videoId}`)[0].contentWindow.postMessage(
         '{"event":"command","func":"' + 'pauseVideo' + '","args":""}',
         '*',
      );
   }
};
