import React, { Component } from 'react';
import { reactLocalStorage } from 'reactjs-localstorage';
import { withRouter } from 'react-router-dom';
import { Layout, Menu, Drawer, Button, Select } from 'antd';
import {
   StarFilled,
   GiftOutlined,
   VideoCameraFilled,
   GiftFilled,
   CrownFilled,
   HomeOutlined,
   LoginOutlined,
   FormOutlined,
   UserOutlined,
   BarsOutlined,
   EyeInvisibleFilled,
   RocketFilled,
   RedditCircleFilled,
   DingdingOutlined,
   ThunderboltFilled,
   GitlabFilled,
   RobotFilled,
   HourglassFilled,
   ReadFilled,
   FireFilled,
   SearchOutlined,
   TagOutlined,
} from '@ant-design/icons';
import dotenv from 'dotenv';
import Profile from '../../containers/Profile/profile';
import SFCINEMA from '../../SFCINEMA.png';
import { requestProfile } from '../../requests';
import './MenuBar.css';
dotenv.config();

const url = require('url');
const { Header } = Layout;
const { SubMenu } = Menu;
const { Option, OptGroup } = Select;

const seriesList = [
   {
      title: ' ',
      key: '/seriesMenuItem-1',
      list: [
         '',
         '',
         '',
         '',
         ' ',
         '',
         '',
         '  ',
         '',
         '',
      ],
   },
   {
      title: ' ',
      key: '/seriesMenuItem-2',
      list: ['', ' ', ' ', ' '],
   },
   {
      title: ' ',
      key: '/seriesMenuItem-3',
      list: ['', '', '', ''],
   },
   {
      title: ' ',
      key: '/seriesMenuItem-4',
      list: [
         ' ',
         '',
         ' ',
         '',
         '',
         ' ',
         '  ',
      ],
   },
   {
      title: ' ',
      key: '/seriesMenuItem-5',
      list: ['   ', '28 ', ''],
   },
];

const genre = [
   { genre: ' ', icon: <RocketFilled /> },
   { genre: '', icon: <RedditCircleFilled /> },
   { genre: ' ', icon: <DingdingOutlined /> },
   { genre: '', icon: <ThunderboltFilled /> },
   { genre: '', icon: <GitlabFilled /> },
   { genre: '   AI', icon: <RobotFilled /> },
   { genre: ' ', icon: <HourglassFilled /> },
   { genre: '', icon: <ReadFilled /> },
   { genre: '', icon: <EyeInvisibleFilled /> },
   { genre: '', icon: <FireFilled /> },
];

const recommended = [
   {
      category: ' ',
      icon: <VideoCameraFilled />,
      path: 'latest-movies',
   },
   {
      category: '  ',
      icon: <StarFilled />,
      path: 'highly-rated-movies',
   },
   {
      category: ' ',
      icon: <GiftFilled />,
      path: 'operator-recommendation',
   },
   {
      category: 'SF ',
      icon: <CrownFilled />,
      path: 'sf-masterpiece',
   },
];

class MenuBar extends Component {
   constructor(props) {
      super(props);
      this.state = {
         visible: false,
         profile: {},
         searchVisible: false,
         dropdown: true,
      };
   }

   handlePageSwitching = (path) => {
      const { history } = this.props;

      switch (path) {
         case 'highly-rated-movies':
            return history.push(`/recommendation/${path}`);

         case 'latest-movies':
            return history.push(`/recommendation/${path}`);

         case 'operator-recommendation':
            return history.push(`/recommendation/${path}`);

         case 'sf-masterpiece':
            return history.push(`/recommendation/${path}`);

         default:
            return;
      }
   };

   handleSettingLogout = () => {
      reactLocalStorage.remove('SFCinemaUserToken');
      location.reload(true);
   };

   hadleSearchVisible = () => {
      this.setState({
         searchVisible: !this.state.searchVisible,
      });
   };

   onBlur = () => {
      this.setState({
         searchVisible: !this.state.searchVisible,
      });
   };

   handleSearchKeword = (keyword) => {
      if (keyword === '') {
         return this.setState({
            dropdown: true,
         });
      }
      this.setState({
         dropdown: false,
      });
      this.props.history.push(`/search?query=${keyword}`);
   };

   handleRecommendedKeyword = (keyword) => {
      this.setState({
         dropdown: true,
      });
      this.props.history.push(`/search?query=${keyword}`);
   };

   handleDrawer = () => {
      this.setState({
         visible: !this.state.visible,
      });
   };

   render() {
      const { isLogin, history } = this.props;
      let path = window.location.pathname;
      let address = url.parse(window.location.search, true).query.key;
      if (path === '/' || path === '/login' || path === '/signup') {
         address = window.location.pathname;
      }

      return (
         <div>
            <Layout className="menubar-layout">
               <Header>
                  <Menu
                     selectedKeys={[address]}
                     mode="horizontal"
                     theme="dark"
                     overflowedIndicator={<BarsOutlined />}
                  >
                     <Menu.Item
                        icon={<img className="header-logo" src={SFCINEMA} />}
                        style={{
                           paddingRight: '30px',
                        }}
                        onClick={() => history.push('/')}
                     />
                     <Menu.Item
                        icon={<HomeOutlined />}
                        key="/"
                        onClick={() => history.push('/')}
                     >
                        
                     </Menu.Item>
                     <SubMenu
                        icon={<GiftOutlined />}
                        title=""
                        mode="horizontal"
                     >
                        {recommended.map((element, i) => (
                           <Menu.Item
                              key={element.path}
                              icon={element.icon}
                              onClick={() =>
                                 this.handlePageSwitching(element.path)
                              }
                           >
                              {element.category}
                           </Menu.Item>
                        ))}
                     </SubMenu>

                     <SubMenu
                        icon={<TagOutlined />}
                        title=""
                        mode="horizontal"
                        key="/genre"
                     >
                        {genre.map((element) => (
                           <Menu.Item
                              key={element.genre}
                              icon={element.icon}
                              onClick={() =>
                                 history.push(`/genre?genre=${element.genre}`)
                              }
                           >
                              {element.genre}
                           </Menu.Item>
                        ))}
                     </SubMenu>
                     <SubMenu
                        icon={<ThunderboltFilled />}
                        title=""
                        mode="horizontal"
                        key="/series"
                     >
                        {seriesList.map((series) => (
                           <SubMenu key={series.key} title={series.title}>
                              {series.list.map((title) => (
                                 <Menu.Item
                                    key={title}
                                    onClick={() =>
                                       history.push(`/series?title=${title}`)
                                    }
                                 >
                                    {title}
                                 </Menu.Item>
                              ))}
                           </SubMenu>
                        ))}
                     </SubMenu>
                     {!isLogin ? (
                        <Menu.Item
                           icon={<FormOutlined />}
                           key="/signup"
                           onClick={() => history.push('/signup')}
                           style={{ float: 'right' }}
                        >
                           
                        </Menu.Item>
                     ) : null}
                     {!isLogin ? (
                        <Menu.Item
                           icon={<LoginOutlined />}
                           key="/login"
                           onClick={() => history.push('/login')}
                           style={{ float: 'right' }}
                        >
                           
                        </Menu.Item>
                     ) : null}
                     {isLogin ? (
                        <Menu.Item
                           icon={<UserOutlined />}
                           key="/profile"
                           onClick={this.handleDrawer}
                           style={{
                              float: 'right',
                           }}
                        >
                            
                        </Menu.Item>
                     ) : null}

                     {this.state.searchVisible ? (
                        <Menu.Item style={{ float: 'right' }}>
                           <Select
                              showSearch={true}
                              defaultOpen={true}
                              autoFocus={true}
                              dropdownClassName={
                                 this.state.dropdown
                                    ? 'dropdown-on'
                                    : 'dropdown-off'
                              }
                              onChange={this.handleRecommendedKeyword}
                              onSearch={this.handleSearchKeword}
                              onBlur={this.onBlur}
                              suffixIcon={<SearchOutlined />}
                              style={{ width: '12.5vw' }}
                              placeholder=", ,  "
                              filterOption={false}
                           >
                              <OptGroup label=" ">
                                 <Option value=""></Option>
                                 <Option value=""></Option>
                                 <Option value=""></Option>
                                 <Option value=" ">
                                     
                                 </Option>
                                 <Option value=" ">
                                     
                                 </Option>
                              </OptGroup>
                           </Select>
                        </Menu.Item>
                     ) : (
                        <Menu.Item
                           icon={<SearchOutlined />}
                           key="/search"
                           style={{ float: 'right' }}
                           onClick={this.hadleSearchVisible}
                        >
                           
                        </Menu.Item>
                     )}
                  </Menu>
               </Header>
            </Layout>

            <Drawer
               title={<img src={SFCINEMA} className="max-small-logo" />}
               width={350}
               closable={true}
               onClose={this.handleDrawer}
               visible={this.state.visible}
               footer={
                  <div className="drawer-profile">
                     <div>
                        <Button
                           type="primary"
                           htmlType="submit"
                           className="btn-signout"
                           onClick={this.handleSettingLogout}
                        >
                           
                        </Button>
                     </div>
                  </div>
               }
            >
               <Profile />
            </Drawer>
         </div>
      );
   }
}
// eslint-disable-next-line
export default withRouter(MenuBar);
// eslint-disable-next-line
