import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { Form, Input, Button, message } from 'antd';
import { FormOutlined } from '@ant-design/icons';
import { requestSignUp, requestBackground } from '../../requests';
import './SignUp.css';

class SignUp extends Component {
   constructor(props) {
      super(props);
   }

   componentDidMount = async () => {
      const background = await requestBackground();
      this.props.handleBackgroundUpdate(background);
   };

   handleSettingSignUp = async () => {
      const {
         loginID,
         password,
         confirmPassword,
         username,
         history,
      } = this.props;

      if (password !== confirmPassword) {
         return message.error('   .');
      } else {
         const result = await requestSignUp(
            loginID,
            password,
            username,
            '',
            'SFCinema',
         );
         if (result !== undefined) {
            message.success(result);
            history.push('/login');
         }
      }
   };

   render() {
      const {
         background,
         history,
         handleInputLoginID,
         handleInputPassword,
         handleInputConfirmPassword,
         handleInputUsername,
      } = this.props;

      return (
         <div>
            <div className="signup-container">
               <div className="box-background-image">
                  <div className="signup-content">
                     <div className="signup-wrap">
                        <div className="signup-box-text">
                           <strong className="signup-text">Sign Up</strong>
                        </div>

                        <Form
                           layout="vertical"
                           name="basic"
                           initialValues={{
                              remember: true,
                           }}
                           onFinish={this.handleSettingSignUp}
                           size="large"
                        >
                           <Form.Item
                              label={
                                 <strong
                                    style={{
                                       color: 'white',
                                    }}
                                 >
                                    
                                 </strong>
                              }
                              name=""
                              rules={[
                                 {
                                    required: true,
                                    message: ' ',
                                 },
                              ]}
                           >
                              <Input onChange={handleInputLoginID} />
                           </Form.Item>

                           <Form.Item
                              label={
                                 <strong
                                    style={{
                                       color: 'white',
                                    }}
                                 >
                                    
                                 </strong>
                              }
                              name=""
                              rules={[
                                 {
                                    required: true,
                                    message: ' ',
                                 },
                              ]}
                           >
                              <Input.Password onChange={handleInputPassword} />
                           </Form.Item>

                           <Form.Item
                              label={
                                 <strong
                                    style={{
                                       color: 'white',
                                    }}
                                 >
                                     
                                 </strong>
                              }
                              name=" "
                              rules={[
                                 {
                                    required: true,
                                    message: ' ',
                                 },
                              ]}
                           >
                              <Input.Password
                                 onChange={handleInputConfirmPassword}
                              />
                           </Form.Item>
                           <Form.Item
                              label={
                                 <strong
                                    style={{
                                       color: 'white',
                                    }}
                                 >
                                    
                                 </strong>
                              }
                              name=""
                              rules={[
                                 {
                                    required: true,
                                    message: ' ',
                                 },
                              ]}
                           >
                              <Input onChange={handleInputUsername} />
                           </Form.Item>

                           <Form.Item
                              style={{
                                 marginBottom: '10px',
                              }}
                           >
                              <Button
                                 type="primary"
                                 htmlType="submit"
                                 icon={<FormOutlined />}
                                 style={{
                                    width: '100%',
                                    marginTop: '10px',
                                 }}
                              >
                                 
                              </Button>
                           </Form.Item>

                           <Form.Item>
                              <Button
                                 type="primary"
                                 htmlType="submit"
                                 icon={<FormOutlined />}
                                 onClick={() => history.push('/login')}
                                 style={{
                                    width: '100%',
                                 }}
                              >
                                  
                              </Button>
                           </Form.Item>
                        </Form>
                     </div>
                  </div>
                  <img
                     className="background-image"
                     src={`https://image.tmdb.org/t/p/w1920_and_h1080_multi_faces/${background[0].backgroundImg}`}
                  />
               </div>
            </div>
         </div>
      );
   }
}

// eslint-disable-next-line
export default withRouter(SignUp);
// eslint-disable-next-line
