import axios from 'axios';
import { message } from 'antd';

const serverUrl = axios.create({
   baseURL: `http://${process.env.REACT_APP_HOST}:5000`,
});

/*  */
export const requestSignUp = async (
   loginID,
   password,
   username,
   profileImg,
   provider,
) => {
   const { data } = await serverUrl
      .post('/user/signup', {
         loginID: loginID,
         password: password,
         username: username,
         profileImg: profileImg,
         provider: provider,
      })
      .catch((err) => {
         console.log(err);
         return message.warning('  .');
      });
   console.log(data);
   return data;
};

/*  */
export const requestLogin = async (loginID, password) => {
   const { data } = await serverUrl
      .post('/user/login', {
         loginID: loginID,
         password: password,
      })
      .catch((err) => {
         console.log(err);
         return message.warning(
            '  ,  .',
         );
      });

   return data;
};

/*  */
export const requestProfile = async (accessToken) => {
   const { data } = await serverUrl.get('/user/profile', {
      headers: {
         Authorization: 'Bearer ' + accessToken,
      },
   });
   if (data === '   .') {
      return message.error(data);
   }

   return data;
};

/*    */
export const requestBackground = async () => {
   const { data } = await serverUrl.get('/background/image');
   return data;
};

